<template>
  <div class="pos">
    <el-row class="wrap">
      <!-- 侧栏 -->
      <el-col :span="7" class="pos-order order-list" id="pos-order">
        <el-tabs class="pos-order-tag">
          <el-tab-pane class="pos-order-tag-content" label="点餐">
            <el-table :data="tableData" border style="width:100%">
              <el-table-column prop="name" label="商品名称"></el-table-column>
              <el-table-column width="80" prop="count" label="数量"></el-table-column>
              <el-table-column width="90" prop="price" label="价格"></el-table-column>
              <el-table-column label="操作" width="92" fixed="right">
                <template slot-scope="scope">
                  <el-button type="text" size="small" @click="addGoods(scope.row)">增加</el-button>
                  <el-button type="text" size="small" @click="delGoods(scope.row)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
            <div class="total">
              <p>
                <small>数量：</small>
                {{totalNumber}}
              </p>
              <p>
                <small>总价：</small>
                {{totalMoney}}
                <small>元</small>
              </p>
            </div>
            <div class="pos-order-tag-btn">
              <el-button type="warning" @click="pendingOrder">挂单</el-button>
              <el-button type="danger" @click="clearGoods">清空</el-button>
              <el-button type="success" @click="checkout">结账</el-button>
            </div>
          </el-tab-pane>
          <el-tab-pane label="挂单">
            <el-table :data="pendingOrderData" border style="width:100%">
              <el-table-column prop="name" label="商品名称"></el-table-column>
              <el-table-column width="80" prop="count" label="数量"></el-table-column>
              <el-table-column width="90" prop="price" label="价格"></el-table-column>
            </el-table>
            <div class="total">
              <p>
                <small>数量：</small>
                {{pendingOrderNumber}}
              </p>
              <p>
                <small>总价：</small>
                {{pendingOrderMoney}}
                <small>元</small>
              </p>
            </div>
            <div class="pos-order-tag-btn">
              <el-button type="warning" @click="clearOrder">消单</el-button>
              <el-button type="success" @click="checkoutOrder">结单</el-button>
            </div>
          </el-tab-pane>
        </el-tabs>
      </el-col>
      <!-- 内容 -->
      <el-col :span="17" class="pos-content">
        <div>
          <!-- 常用商品 -->
          <div class="pos-content-head">全部商品</div>
          <!-- info-head -->
          <div>
            <ul class="ul-list">
              <li v-for="(item,index) in offenGoods" :key="index" @click="addGoods(item)">
                <span>{{item.name}}</span>
                <span class="li-piace">￥{{item.price}}</span>
              </li>
            </ul>
          </div>
          <!-- info-content -->
          <div>
            <el-tabs>
              <el-tab-pane label="快餐">
                <ul class="ul-goods">
                  <li
                    class="li-goods"
                    v-for="(item,index) in fastFoods"
                    :key="index"
                    @click="addGoods(item)"
                  >
                    <div class="li-goods-picture">
                      <img :src="item.img" width="50" height="50" />
                    </div>
                    <div class="li-goods-info">
                      <p class="li-goods-info-name">{{item.name}}</p>
                      <p class="li-goods-info-piace">￥{{item.price}}</p>
                    </div>
                  </li>
                </ul>
              </el-tab-pane>
              <el-tab-pane label="主食">
                <ul class="ul-goods">
                  <li
                    class="li-goods"
                    v-for="(item,index) in stapleFood"
                    :key="index"
                    @click="addGoods(item)"
                  >
                    <div class="li-goods-picture">
                      <img :src="item.img" width="50" height="50" />
                    </div>
                    <div class="li-goods-info">
                      <p class="li-goods-info-name">{{item.name}}</p>
                      <p class="li-goods-info-piace">￥{{item.price}}</p>
                    </div>
                  </li>
                </ul>
              </el-tab-pane>
              <el-tab-pane label="饮品">
                <ul class="ul-goods">
                  <li
                    class="li-goods"
                    v-for="(item,index) in drink"
                    :key="index"
                    @click="addGoods(item)"
                  >
                    <div class="li-goods-picture">
                      <img :src="item.img" width="50" height="50" />
                    </div>
                    <div class="li-goods-info">
                      <p class="li-goods-info-name">{{item.name}}</p>
                      <p class="li-goods-info-piace">￥{{item.price}}</p>
                    </div>
                  </li>
                </ul>
              </el-tab-pane>
            </el-tabs>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import leftNav from "@/components/common/leftNav";
export default {
  name: "Pos",
  components: {
    leftNav
  },
  data() {
    return {
      pendingOrderMoney: 0,
      pendingOrderNumber: 0,
      totalMoney: 0,
      totalNumber: 0,
      tableData: [],
      pendingOrderData: [],
      offenGoods: [
        {
          id: 1,
          name: "薯条",
          price: 8,
          count: 1
        },
        {
          id: 2,
          name: "披萨",
          price: 14,
          count: 1
        },
        {
          id: 3,
          name: "汉堡",
          price: 13,
          count: 1
        },
        {
          id: 4,
          name: "面条",
          price: 12,
          count: 1
        },
        {
          id: 5,
          name: "西餐",
          price: 18,
          count: 1
        },
        {
          id: 6,
          name: "方便面",
          price: 7,
          count: 1
        },
        {
          id: 7,
          name: "鱼",
          price: 11,
          count: 1
        },
        {
          id: 8,
          name: "热饮",
          price: 5,
          count: 1
        },
        {
          id: 9,
          name: "牛奶",
          price: 5,
          count: 1
        },
        {
          id: 10,
          name: "咖啡",
          price: 5,
          count: 1
        },
        {
          id: 11,
          name: "可乐",
          price: 4,
          count: 1
        }
      ],
      fastFoods: [
        {
          id: 1,
          name: "薯条",
          price: 8,
          count: 1,
          img: require("../../assets/img/食物&器皿－薯条.png")
        },
        {
          id: 2,
          name: "披萨",
          price: 14,
          count: 1,
          img: require("../../assets/img/食物&器皿－披萨.png")
        },
        {
          id: 3,
          name: "汉堡",
          price: 13,
          count: 1,
          img: require("../../assets/img/食物&器皿－汉堡.png")
        }
      ],
      stapleFood: [
        {
          id: 4,
          name: "面条",
          price: 12,
          count: 1,
          img: require("../../assets/img/食物,面条.png")
        },
        {
          id: 5,
          name: "西餐",
          price: 18,
          count: 1,
          img: require("../../assets/img/食物&器皿－西餐.png")
        },
        {
          id: 6,
          name: "方便面",
          price: 7,
          count: 1,
          img: require("../../assets/img/食物&器皿－方便面.png")
        },
        {
          id: 7,
          name: "鱼",
          price: 11,
          count: 1,
          img: require("../../assets/img/食物&器皿－鱼.png")
        }
      ],
      drink: [
        {
          id: 8,
          name: "热饮",
          price: 5,
          count: 1,
          img: require("../../assets/img/食物&器皿－热饮.png")
        },
        {
          id: 9,
          name: "牛奶",
          price: 5,
          count: 1,
          img: require("../../assets/img/食物&器皿－牛奶.png")
        },
        {
          id: 10,
          name: "咖啡",
          price: 5,
          count: 1,
          img: require("../../assets/img/食物&器皿－咖啡.png")
        },
        {
          id: 11,
          name: "可乐",
          price: 4,
          count: 1,
          img: require("../../assets/img/食物&器皿－可乐.png")
        }
      ]
    };
  },
  methods: {
    uniformNumAndMoney() {
      this.totalMoney = 0;
      this.totalNumber = 0;
      this.tableData.forEach(items => {
        this.totalNumber += items.count;
        this.totalMoney += items.price * items.count;
      });
    },
    // 添加商品
    addGoods(good) {
      // 商品是否已经存在订单列表中
      let isHave = false;
      for (const element of this.tableData) {
        if (element.id === good.id) {
          isHave = true;
        }
      }
      // 根据判断的值添加商品
      if (isHave) {
        // 改变数量
        let arr = this.tableData.filter(item => item.id === good.id);
        arr[0].count++;
      } else {
        let newGoods = {
          id: good.id,
          name: good.name,
          price: good.price,
          count: good.count
        };
        this.tableData.push(newGoods);
      }
      this.uniformNumAndMoney();
    },
    // 删除商品
    delGoods(good) {
      this.tableData = this.tableData.filter(item => item.id != good.id);
      this.uniformNumAndMoney();
    },
    // 清空商品
    clearGoods() {
      if (this.totalNumber != 0) {
        this.totalNumber = 0;
        this.totalMoney = 0;
        this.tableData = [];
      }
    },
    // 结账
    checkout() {
      if (this.totalNumber != 0) {
        this.$message({
          message: "结账成功!",
          type: "success"
        });
        this.totalNumber = 0;
        this.totalMoney = 0;
        this.tableData = [];
        this.pendingOrderNumber = 0;
        this.pendingOrderMoney = 0;
        this.pendingOrderData = [];
      } else {
        this.$message.error("未选择商品,不能结账");
      }
    },
    // 挂单
    pendingOrder() {
      if (this.pendingOrderData.length != 0) {
        this.$message({
          message: "尚未消单,不能继续挂单!",
          type: "warning"
        });
      } else {
        if (this.totalNumber != 0) {
          this.$message({
            message: "挂单成功!",
            type: "success"
          });
          for (const ele of this.tableData) {
            this.pendingOrderData.push(ele);
          }
          this.pendingOrderMoney = 0;
          this.pendingOrderNumber = 0;
          this.pendingOrderData.forEach(items => {
            this.pendingOrderNumber += items.count;
            this.pendingOrderMoney += items.price * items.count;
          });
        } else {
          this.$message.error("未选择商品,不能挂单");
        }
      }
    },
    // 结单
    checkoutOrder() {
      if (this.pendingOrderNumber != 0) {
        this.$message({
          message: "结单成功!",
          type: "success"
        });
        this.pendingOrderNumber = 0;
        this.pendingOrderMoney = 0;
        this.pendingOrderData = [];
      } else {
        this.$message.error("未选择商品,不能结账");
      }
    },
    // 消单
    clearOrder() {
      if (this.pendingOrderNumber != 0) {
        this.$message({
          message: "消单成功!",
          type: "success"
        });
        this.pendingOrderNumber = 0;
        this.pendingOrderMoney = 0;
        this.pendingOrderData = [];
      }
    }
  }
};
</script>

<style scoped>
.pos,
.pos > .wrap {
  height: 100%;
}
/* 侧栏样式 */
.pos-order {
  height: 100%;
  background-color: #fff;
  border-right: 1px solid #d5d9e3;
}
.pos-order-tag-btn {
  margin-top: 10px;
}
.total {
  /* background-color: red; */
  display: flex;
  justify-content: center;
  height: 40px;
  line-height: 40px;
}
.total > p {
  padding: 0;
  margin: 0;
  margin-right: 15px;
}
/* content样式 */
.pos-content {
  background-color: #eff3f9;
  height: 100%;
}
.pos-content-head {
  background-color: #fff;
  height: 38px;
  border-bottom: 2px solid #e4e7ed;
  text-align: left;
  line-height: 38px;
  padding-left: 5px;
}
.ul-list {
  overflow: hidden;
}
.ul-list > li {
  float: left;
  border: 1px solid #e4e7ed;
  width: 140px;
  height: 50px;
  margin: 10px;
  padding: 2px;
  line-height: 50px;
  background-color: #fff;
  font-size: 20px;
  cursor: pointer;
}
.li-piace {
  color: cornflowerblue;
}
.ul-goods {
  overflow: hidden;
}
.li-goods {
  border: 1px solid #e4e7ed;
  height: 50px;
  width: 140px;
  display: flex;
  padding: 5px;
  background-color: #fff;
  margin: 10px;
  float: left;
  cursor: pointer;
}
.li-goods-picture {
  height: 50px;
}
.li-goods-info {
  height: 50px;
}
.li-goods-info-name {
  height: 30px;
  margin: 0;
  padding: 0 4px;
  line-height: 30px;
  color: #ffb957;
}
.li-goods-info-piace {
  height: 20px;
  margin: 0;
  padding: 0 4px;
  line-height: 20px;
}
</style>



